<template>
  <div id="adddomitory">
    <div class="weui-cells__title"></div>
    <div class="weui-cells weui-cells_form">
      <div class="weui-cell">
        <div class="weui-cell__hd">
          <label class="weui-label"></label>
        </div>
        <div class="weui-cell__bd">
          <input class="weui-input" type="text" pattern="[0-9]*" placeholder=":1-1-111" v-model="groupNo" value="" />
        </div>
      </div>
      <div class="weui-cell">
        <div class="weui-cell__hd">
          <label class="weui-label"></label>
        </div>
        <div class="weui-cell__bd">
          <input class="weui-input" type="text" pattern="[0-9]*" placeholder="" v-model="userName" value="" />
        </div>
      </div>
      <div class="weui-cell">
        <div class="weui-cell__hd">
          <label class="weui-label"></label>
        </div>
        <div class="weui-cell__bd">
          <input class="weui-input" type="number" pattern="[0-9]*" placeholder="" v-model="userPhone" value="" />
        </div>
      </div>
    </div>
    <div class="weui-cell weui-cell_select weui-cell_select-after">
      <div class="weui-cell__hd">
        <label for="" class="weui-label"></label>
      </div>
      <div class="weui-cell__bd">
        <select class="weui-select" name="select2" @change="getAll($event)">
          <option v-for="(h,k) in this.index" :key="k" :value="h.indexId">{{h.name}}</option>
        </select>
      </div>
    </div>
    <a href="javascript:;" class="weui-btn weui-btn_primary" @click="subBox"></a>
  </div>
</template>

<script>
import axios from "axios";
import qs from "qs";
import weui from "weui.js";
var config = require("../../../../config");
config = process.env.NODE_ENV === "development" ? config.dev : config.build;
export default {
  data() {
    return {
      index: [
        {
          indexId: 0,
          name: ""
        },
        {
          indexId: 1,
          name: ""
        },
        {
          indexId: 2,
          name: ""
        },
        {
          indexId: 3,
          name: ""
        },
        {
          indexId: 4,
          name: ""
        },
        {
          indexId: 5,
          name: ""
        },
        {
          indexId: 6,
          name: ""
        },
        {
          indexId: 7,
          name: ""
        },
        {
          indexId: 8,
          name: ""
        }
      ],
      num: "",
      name: "",
      phone: "",
      district: "",
      token: "",
      groupNo: "",
      userName: "",
      userPhone: ""
    };
  },
  created() {
    this.token = this.getCookie("token");
  },
  methods: {
    getAll(event) {
      this.district = event.target.value;
      //console.log(this.group);
    },
    subBox() {
      let group = this.groupNo[0];
      console.log(group);
      console.log(typeof group);
      console.log(typeof this.district);
      if (this.groupNo === "") {
        weui.topTips("!", 1000);
      } else if (this.userName === "") {
        weui.topTips("!", 1000);
      } else if (this.userPhone === "") {
        weui.topTips("!", 1000);
      } else if (this.district === "") {
        weui.topTips("");
      } else if (group !== this.district) {
        weui.topTips(this.district + "");
      } else {
        let obj = {
          token: this.token,
          groupNo: this.groupNo,
          userName: this.userName,
          userPhone: this.userPhone,
          groupDistrict: this.district
        };
        let send = qs.stringify(obj);
        axios
          .post(config.sellerUrl + "/sell/seller/group/save", send, {
            headers: {
              "Content-Type": "application/x-www-form-urlencoded"
            }
          })
          .then(res => {
            console.log(res);
            if (res.data.msg === "") {
              weui.toast("!", {
                duration: 1000
              });
              this.groupNo = "";
              this.userName = "";
              this.userPhone = "";
            }
          })
          .catch(err => {
            weui.alert("!");
            /*$.toast("","cancel",{
            duration:2000,
          });*/
            console.log(err);
          });
      }

      //$.toast("!");
    }
  }
};
</script>

<style>
</style>



